@font-face {
  src: url("helvetica.27203c3f.woff2");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  src: url("helvetica-bold.4c13dce9.woff2");
  font-style: normal;
  font-weight: 700;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--primary-text-color);
  font-family: Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

li {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
}

:root {
  --primary-text-color: #5c5c5e;
  --primary-gradient: linear-gradient(180deg, #bc10d8 0%, rgba(80, 0, 250, .93) 100%);
  --white-color: #fff;
  --white-text-color: #f6f4f5;
  --secondary-button-bg-color: #d9d9d9;
}

.header {
  height: 80px;
  border-bottom: 1px solid #5c5c5e;
  padding-top: 19px;
  padding-bottom: 19px;
}

.nav {
  justify-content: space-between;
  display: flex;
}

.menu {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.basket {
  display: flex;
  position: relative;
}

.basket--on span {
  width: 15px;
  height: 15px;
  background: var(--primary-gradient);
  color: var(--white-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 11px;
  line-height: 0;
  display: flex;
  position: absolute;
  bottom: 4px;
  right: -2px;
}

.hero {
  min-height: calc(100vh - 81px);
  background-image: url("hero-bg.78117c03.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero__title {
  padding-top: 230px;
  font-size: 96px;
  line-height: 1.14;
}

.hero__subtitle {
  margin-bottom: 5px;
  font-size: 36px;
  line-height: 1.14;
}

.hero__text {
  margin-bottom: 50px;
  font-size: 13px;
  line-height: 15px;
}

.hero__buy-text {
  padding-top: 6px;
  font-size: 13px;
  line-height: 15px;
}

.buy-button {
  cursor: pointer;
  background: var(--primary-gradient);
  text-transform: uppercase;
  border: none;
  padding: 1px;
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  display: flex;
}

.buy-button__text {
  color: var(--white-text-color);
  padding: 20px 110px;
}

.buy-button__price {
  color: var(--primary-text-color);
  background-color: var(--secondary-button-bg-color);
  padding: 20px 105px;
}

